@Injectable({
  providedIn: "root",
})
export class ApiService {
  BASE_URL: any = this.url.SERVER_URL + '/api/';
  apiEndPoints: any;
  countryCode: any;

  constructor(
    private http: HttpClient,
    private url: UrlService,
    private comm: CommonService
  ) {
    this.apiEndPoints = {
      //Admin
      adminLogin: 'admin/login',
      adminLogout:'admin/logout'
    }
    for (let key in this.apiEndPoints) {
      this.apiEndPoints[key] = this.BASE_URL + this.apiEndPoints[key];
    }
    this.getCountryCode();
  }

  protected getHeaders(): { headers: HttpHeaders } {
    return {
      headers: new HttpHeaders({
        'Accept': 'application/json',
        'Authorization': 'Bearer '+this.getToken()
      })
    };
  }
  getCountryCode() {
    return this.http
      .get<Response>("assets/json/countryCode.json")
      .pipe(map(response => response));
  }
  singIn(body: LoginBody):Observable<any>{
   const data:FormData=new FormData();
   data.append('email',body.email);
   data.append('password',body.password);
    return this.http
    .post<any>(this.apiEndPoints.adminLogin, data)
    .pipe(
      catchError(this.handleError<any>('Login'))
    );
  }
  singOut():Observable<any>{
     return this.http
     .post<any>(this.apiEndPoints.adminLogout,{},this.getHeaders())
     .pipe(
       catchError(this.handleError<any>('Logout'))
     );
   }
  setUser(user:any){
  localStorage.setItem("Rupee_Admin", user);
  }
  sendToken(token: string) {
    localStorage.setItem("token", token);
  }
  getToken() {
    return localStorage.getItem("token");
  }
  isLoggedIn() {
    return this.getToken() !== null;
  }
  // Error handling
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
      return;
    };
  }
}
