import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from "@angular/material/dialog";
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl
} from "@angular/forms";
import { ApiService } from 'src/app/idealz/services/api/api.service';
import { ProductService } from 'src/app/idealz/services/product.service';
@Component({
  selector: 'app-add-edit-inventory',
  templateUrl: './add-edit-inventory.component.html',
  styleUrls: ['./add-edit-inventory.component.scss']
})
export class AddEditInventoryComponent implements OnInit {
  productForm: FormGroup;
  productImage: any;
  submitted: boolean = false;
  constructor(private formBuilder: FormBuilder, private productService: ProductService, private dialog: MatDialogRef<AddEditInventoryComponent>) {
   }

  ngOnInit() {
    this.productForm = this.formBuilder.group({
      name: new FormControl("", [Validators.required, Validators.maxLength(20), Validators.pattern(".*\\S.*[a-zA-z0-9 ]")]),
      arabicName: new FormControl(""),
      description: new FormControl("", [Validators.required, Validators.maxLength(250)]),
      arabicDescription: new FormControl(""),
      image: new FormControl("", [Validators.required]),
      status: new FormControl(),
      prize: new FormControl("", Validators.required)
    });
  }

  async profilePic(event) {
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        this.productImage = event.target.result;
        this.productForm.controls['image'].setValue(this.productImage);
      };
    }
  }

  onSubmit() {
    this.submitted = true;
    if (this.submitted && this.productForm.valid) {
      var data = this.productForm.value;
      //data['id']="34143"
      //data['id'] = Date.now() + Math.random().toString().slice(2);
      this.productService.addProduct(data).subscribe(res => {
      })
      this.dialog.close(data);
    }
  }

  close() {
    this.dialog.close();
  }
}
